@media (prefers-color-scheme: dark) {
  body {
    background-color: #262626;
    color: #f3f3f3;
    font-weight: 100;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

body {
  padding: 1.5em;
  margin: auto;
}

blockquote {
  font-style: italic;
}

h1 {
  font-family: "Georgia", serif;
  font-size: 44px;
  line-height: 48px;
  margin: auto auto 30px;
  max-width: 40rem;
  margin-top: 50vh;
  font-weight: 100;
}

hr {
  border: none;
  height: 1px;
  background-color: #ddd;
  margin: auto auto 5rem;
  max-width: 40rem;
  margin-top: 5rem;
  margin-bottom: 5rem;
}

h2 {
  font-family: "Georgia", serif;
  font-size: 30px;
  line-height: 48px;
  margin: auto auto 30px;
  max-width: 40rem;
  margin-top: 50vh;
  letter-spacing: 4px;
  font-weight: 100;
}

p {
  font-family: -apple-system-ui-serif, ui-serif, "Georgia", serif;
  font-size: 18px;
  line-height: 30px;
  margin: auto auto 24px;
  max-width: 40rem;
}
